import { Component, OnInit, OnDestroy } from '@angular/core';
import { Experiment } from '../experiment/experiment.model';
import { Subscription } from 'rxjs';
import { ExperimentsService } from '../experiment/experiments.service';

@Component({
  selector: 'app-section-about',
  templateUrl: './section-about.component.html'
  // styleUrls: ['./section-about.component.css']
})
export class SectionAboutComponent implements OnInit, OnDestroy {

  introtext =
    {
      introtitle: 'Into the unknown',
      introtext: 'Forsgsstationen har siden 2009 huset og kurateret en lang rkke kunstnere og deres forsg. Alle genrer har vret reprsenteret inden for den scenekunstneriske palet, men ogs nye genrer er opstet som et udtryk for den fantastiske opfindsomhed der prger disse unikke, markante og modige forsg. Vidensdeling er et centralt begreb p Forsgsstationen og betyder, at forsgsresultater konsekvent deles gennem visninger, rapporter, platforme og seminarer. Det nsker vi at udvide yderligere med dette site. Into the unknown er en ben source, hvor fagfolk og andre interesserede kan f en let adgang til eksempler p forsgsarbejde i ord og praksis. Hvert forsg prsenteres gennem interview og film for at give bde en intellektuel og sanselig adgang til forsgsarbejdet. Det er Forsgsstationens ambition p sigt, at udvide dette site med eksempler p nye forsg. Velkommen Into the unknown!'
    }

    experiments: Experiment[] = [];
    private experimentsSub: Subscription;
    constructor(public experimentsService: ExperimentsService) {}

    ngOnInit() {
      this.experimentsService.getExperiments();
      this.experimentsSub = this.experimentsService.getExperimentsUpdateListener()
        .subscribe((experiments: Experiment[]) => {
          experiments.sort((expA, expB) => (expA.year > expB.year) ? -1 : 1);
          // console.log(experiments);
          this.experiments = experiments;
        });
    }

    ngOnDestroy() {
      if (this.experimentsSub) {
        this.experimentsSub.unsubscribe();
      }
    }


}
